import React, { useEffect, useState } from 'react';
import ReactFrappeChart from 'react-frappe-charts';


export function Graph(props){
    const {weightMap, pointIdentifier} = props;

    console.log(pointIdentifier);

    return(
        <ReactFrappeChart
        className="graph"
        type="line"
        colors={["#CDB4DB"]}
        axisOptions={{ xAxisMode: "tick", yAxisMode: "tick", xIsSeries: 1 }}
        height={200}
        data={{
            labels: Array.from(weightMap.keys()).map((time) => new Date(time).toDateString()),
            datasets: [{name: pointIdentifier, type:"line", values: Array.from(weightMap.values()).map((value) => parseFloat(value)) }]
        }}
        />
    )
}
